@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700;800;900&display=swap');

.leaderboard-entry {
    width: 100%;
    display: flex;
    justify-content: start;
    align-items: center;
    box-shadow: inset 0px -1px 0px #DBE4EF;
    /*cursor: pointer;*/
}

.pointer-available{
    cursor: pointer;
}

.leaderboard-profile-image {
    width: 2em;
    height: 2em;
    border-radius: 100%;
    background-color: var(--dim-white);
}

.leaderboard-points{
    margin-left: 1rem;
}

.teamCount{
    background-color: lightgrey;
    padding: 2px;
    margin-left: 5px;
    font-size: 0.8rem;
}

/*  lleaderboard width controller for all component */
.leaderboard-Main-Controller{
    display: flex;
    max-width: 100%;
    width: 100%;
    text-align: start;
    
}

.leaderboard-Main-Controller-Tab3{
display: flex;
max-width: 100%;
width: 100%;
text-align: start;
justify-content: start;
align-items: center;
box-shadow: inset 0px -1px 0px #DBE4EF;
max-height: 70px;
height: 100%;
}
.leaderboard-Main-Controller-Rank{
   
    max-width: 15%;
    width: 100%;
    text-align: start;
}

.leaderboard-Main-Controller-UserName{   
    max-width: 35%;
    width: 100%;
    transition: 0.2s all ease-in-out;
    text-align: start;
}
.leaderboard-Main-Controller-TeamCount{   
    max-width: 25%;
    width: 100%;
    text-align: start;
}
.leaderboard-Main-Controller-Porfolio{   
    max-width: 25%;
    width: 100%;
    text-align: start;
}

.teamCounttemp{
    background-color: lightgrey;
    padding: 2px;
   text-align: center;
    font-size: 0.8rem;
}

.totalPoints{
    display: flex;
    justify-content: center;
    font-size: 1rem;
}

.leaderboard-username {
    /* max-width: 6.25em;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis; */
    transition: 0.2s all ease-in-out;
    /* margin-left: 3rem; */
}

.leaderboard-username:hover {
    /*cursor: pointer;*/
    transition: 0.2s all ease-in-out;
}

.leaderboard-teamname {
    max-width: 6.25em;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    transition: 0.2s all ease-in-out;
}

.leaderboard-teamname:hover {
    cursor: pointer;
    transition: 0.2s all ease-in-out;
}
