:root {
  --dim-violet-blue: #463df1eb;
  --violet-blue: #453df1;
  --golden: #fea31b;
  --dim-golden: #fea31b78;
  --white: #ffffff;
  --black-shade: #071f36;
  --dim-white: #d2e0ee;
  --grey-shade: #4c6781;
  --green: #28ad35;
  --dark-dim-white: #869EB6;
  --black: #000000;
  --light-matte-black: #1e222d;
  /* --tournament-card-shadow:rgba(0, 0, 0, 0.35) 0px 5px 15px; */
  --tournament-card-shadow :rgba(0, 0, 0, 0.24) 0px 3px 8px;
  /* --coin-card-shadow:rgba(0, 0, 0, 0.07) 0px 1px 2px, rgba(0, 0, 0, 0.07) 0px 2px 4px, rgba(0, 0, 0, 0.07) 0px 4px 8px, rgba(0, 0, 0, 0.07) 0px 8px 16px, rgba(0, 0, 0, 0.07) 0px 16px 32px, rgba(0, 0, 0, 0.07) 0px 32px 64px; */
  --coin-card-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}
body {
  margin: 0;
  /* min-height: calc(var(--vh, 1vh) * 100); */
  font-family: 'poppins' !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-y: hidden;
  overflow-x: hidden;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.loading-component {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.overlay {
  background-color: rgb(0,0,0);
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0.7;
  z-index: 6;
  transition: all 0.3s ease-in-out;
}
.overlay-login {
  background-color: rgb(0,0,0);
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0.7;
  transition: all 0.3s ease-in-out;
}
.fullpage{
  font-family: "poppins" !important;
}

  @-webkit-keyframes Pfadeout {
    from {-webkit-transform:translateY(0);transform:translateY(0); opacity: 1;} 
    to {-webkit-transform:translateY(500px);transform:translateY(500px); opacity: 0;}
  }
  
  @keyframes Pfadeout {
    from {-webkit-transform:translateY(0);transform:translateY(0); opacity: 1;} 
    to {-webkit-transform:translateY(500px);transform:translateY(500px); opacity: 0;}
  }
  .fade-out-policies{
    -webkit-animation: Pfadeout 0.5s ease-in-out;
            animation: Pfadeout 0.5s ease-in-out;
  }
  @-webkit-keyframes Pfadein {
    from {-webkit-transform:translateY(500px);transform:translateY(500px); opacity: 0;}
    to {-webkit-transform:translateY(0);transform:translateY(0); opacity: 1;} 
  }
  
  @keyframes Pfadein {
    from {-webkit-transform:translateY(500px);transform:translateY(500px); opacity: 0;}
    to {-webkit-transform:translateY(0);transform:translateY(0); opacity: 1;} 
  }
  .fade-in-policies{
    -webkit-animation: Pfadein 0.5s ease-in-out;
            animation: Pfadein 0.5s ease-in-out;
  }
  .folio-privacy-policies{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    align-content:center;
    position: absolute;
    color: var(--grey-shade);
    scrollbar-width: none;
    z-index: 7;
  }
  #policies-div{
    width: 60%;
    height: 70%;
    border-radius: 12px;
    box-shadow: 24;
    position: absolute;
    background-color: var(--dim-white);
    color: var(--grey-shade);
    overflow-y: scroll;
    scrollbar-width: none;
    z-index: 10;
    padding: 16px;
  }
  @media (max-width:600px){
    #policies-div{
    width: 90%;
    }
