}

.profileHeader {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  height: 90%;
  width: 90%;
  flex-wrap: wrap;
}

.profilePicture {
  height: 4rem;
  width: 4rem;
  border-radius: 100%;
  margin-right: 2rem;
  overflow: hidden;
  background-color: #111;
  position: relative;
  border: 2px solid aliceblue;
}
/* Profile Pic */
.profilePicture:hover .profilepic__content {
  opacity: 1;
}

.profilePicture:hover .profilepic__image {
  opacity: 0.5;
}

.profilepic__image {
  object-fit: cover;
  opacity: 1;
  transition: opacity 0.2s ease-in-out;
}

.profilepic__content {
  z-index: 100;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white;
  opacity: 0;
  transition: opacity 0.2s ease-in-out;
  cursor: pointer;
}

.profilepic__icon {
  color: white;
  align-items: center;
  display: flex;
  justify-content: center;
}
.profilepic__text {
  text-transform: uppercase;
  font-size: 5px;
  width: 50%;
  text-align: center;
}
